import '../App.css';
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Alert from 'react-bootstrap/Alert';
import moment from 'moment';
export default function Upcomingmatch() 
{
  const [scores, setScores] = useState([]);  
  const [json, setjson] = useState([]);  
  useEffect(() => {
    async function fetchScores() {
    const response = await axios.get('https://www.hindustantimes.com/static-content/10s/hockey/hockey_liupre.json');
    setScores(response.data.upcoming);
    }
    fetchScores();
    let data = require('../countries.json');
    const json = Object.fromEntries(
    Object.entries(data).map(([key, value]) => [value, key])
);
    setjson(json) 
}, []);
  return (
    <div className='upcomingmatch'>    
    <h1 className='title'>Upcoming Matches</h1>
      {scores.map(upcoming =>(
      <Alert>
      <Alert.Heading><h2>FIH Odisha Hockey Men’s World Cup 2023</h2>{upcoming.venue}</Alert.Heading>
      <div className='d-flex justify-content-evenly'>
         
        <div class="card" >
        <div class="card-header">
        <div className='flagpng'><img src={`./png100px/${json[upcoming.teama]}.png`} alt={json[upcoming.teama]}/></div>
        </div>
          <ul class="list-group list-group-flush">
             <li class="list-group-item"><div className='teamnames'>
        <h2>{upcoming.teama}</h2>
        <p>GOALS : {upcoming.teama_score}</p>
        </div> </li>          
             </ul>
          </div>
          <div class="card" >
        <div class="card-header">
        <div className='flagpng'><img src={`./png100px/${json[upcoming.teamb]}.png`} alt={json[upcoming.teamb]}/></div>
        </div>
          <ul class="list-group list-group-flush">
             <li class="list-group-item"><div className='teamnames'>
        <h2>{upcoming.teamb}</h2>
        <p>GOALS : {upcoming.teamb_score}</p>
        </div> </li>          
             </ul>
          </div>
        </div> 
      {/* <hr/> */}
      <p className="mb-0 ,border-text">
        <b>Date :</b> {moment(upcoming.matchdate_local).format("DD/MM/YYYY")} | <b>{upcoming.match_result}</b> | {upcoming.match_status}
      </p>
    </Alert>            
      ))}    
    </div>
  );  
  }
